import React, { Component } from 'react';

import Display from '../Display/Display';
import Button from '../Button/Button';

import './Random.css';

class Random extends Component {

    constructor () {
        super();

        this.state = {
            mainValue: 0
        }
    }

    addNextNumber =  () => {
        const randomValue = Math.random();

        this.setState((prevState)=>({
            mainValue: prevState.mainValue + randomValue
        }));

    }

    componentWillMount () {
        this.addNextNumber();
    }

    render () {
        return (
            <div className = 'random-container'>
                <Display value={this.state.mainValue}/>
                <Button changeRandomValue={this.addNextNumber}/>
            </div>
        )
    }
}

export default Random;